export default{
	name:'searchs',
	data() {
		return {
			setdata: [],
			searchdata:'',
			ifhistory:false,
			localdata:[]  ,//搜索结果
			nonedata:false
		}
	},
	
	methods:{
		// 搜索历史发起搜索
		menubtn(item){
			this.ifhistory = false
			// 查询数据库
			this.searchData(item)
		},
		
		// 按钮搜索
		seArch(){
			let searchkey = this.searchdata
			if(searchkey != ""){
				this.ifhistory = false
				console.log(this.searchdata)
				// 搜索历史本地缓存
				this.getStorage(searchkey)
				// 查询数据库
				this.searchData(searchkey)
			}
		},
		// 回车键搜索
		onKeyInput(){
			let searchkey = this.searchdata
			if(searchkey != ""){
				this.ifhistory = false
				console.log(this.searchdata)
				// 搜索历史本地缓存
				this.getStorage(searchkey)
				// 查询数据库
				this.searchData(searchkey)
			}
		},
		
		// 本地存储
		getStorage(searchkey){
			const seararray = uni.getStorageSync('search_key') || []
			seararray.unshift(searchkey)//插入到数组开头
			uni.setStorageSync('search_key', seararray);
		},
		// 取出搜索历史缓存
		setStorage(){
			let setdata = uni.getStorageSync('search_key');
			// 数组去重new Set只针对于数组字符串
			let setdataarr = Array.from(new Set(setdata))
			console.log(setdataarr)
			if(setdataarr == ''){
				this.ifhistory = false
			}else{
				this.setdata = setdataarr
				this.ifhistory = true
			}
		},
		
		// 清除搜索历史
		removeStorage(){
			uni.removeStorageSync('search_key')
			this.setStorage()
		},
		
		// 发起搜索请求
		// 查询数据库
		searchData(searchkey){
			const db = wx.cloud.database()
			const searchdata = db.collection('userdata')
			searchdata.where({
				datainfo:{
					tipsdata:db.RegExp({
						regexp:searchkey,
						options:'m'
					})
				}
			})
			.get()
			.then((res)=>{
				console.log(res)
				if(res.data.length === 0){
					this.nonedata = true
					this.localdata = ''
				}else{
					this.nonedata = false
					this.localdata = res.data
				}
				
			})
			.catch((err)=>{
				console.log(err)
			})
		},
		
		// 跳转到详情页
		localCont(id){
			console.log(id)
			uni.navigateTo({
				url:'../details/detalis?id=' + id
			})
		}
	},
	
	// 取出本地缓存
	created() {
		this.setStorage()
	}
}
